#code-editor-resizeable {
  z-index: 1000;
  background-color: #464646;
  border: none;
  box-shadow: 2px 2px 5px; }

.ace-editor {
  box-sizing: border-box; }

.code-editor-drag-handle {
  background-color: #535353;
  width: 100%;
  height: 30px;
  cursor: -webkit-grab;
  cursor: grab; }

.code-editor-title {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #FFFFFF;
  float: left;
  height: 100%;
  margin-left: 6px; }

.code-editor-information {
  margin-left: 4px;
  color: #ADADAD; }

.code-editor-information.code-editor-error-information {
  color: #FF5372; }

.code-editor-body {
  display: flex;
  position: absolute;
  width: 100%;
  height: calc(100% - 30px);
  overflow: hidden; }

.code-editor-code-panel {
  width: 100%;
  height: 100%;
  float: left; }

.code-editor-close-button {
  height: 100%;
  width: 30px;
  padding: 2px;
  float: right; }

.code-editor-unscriptable-warning {
  width: 100%;
  height: 100%;
  background-color: #919191;
  color: #626262;
  padding-left: 6px; }

.code-editor-details-panel {
  display: flex;
  flex-direction: column;
  width: 150px;
  height: 100%;
  float: left;
  flex-grow: 0;
  flex-shrink: 0; }

#code-editor-reference {
  width: 150px;
  height: 100%;
  background-color: red;
  float: none; }

